import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import {
  Router,
  hashHistory,
  Route,
  Link
} from 'react-router';
import axios from 'axios';
import {Panel, Navbar, Nav, NavItem} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import {Jumbotron} from 'react-bootstrap';
import {Button} from 'react-bootstrap';
import {Col, Grid, Row, PageHeader} from 'react-bootstrap';

class App extends Component {
  render() {
    return (
      <Router history={hashHistory}>
        <div>
          <h1>Finally something that works</h1>
          <Route path='/' component={Home}/>
          <Route path='/about' component={About}/>
          <Route path='/diary' component={Diary}/>
        </div>
      </Router>
    )
  }
}

class Home extends Component {
  render() {
    return (
      <div>
        <NavBar />
        <Grid fluid>
         <Row>
          <Col xs={12}>
        <Jumbotron>
          <h1>The Foodiary</h1>
          <p>The beginnings of a great food diary</p>
          <Button bsStyle='success'>Do it</Button>
        </Jumbotron>
        </Col>
        </Row>
        </Grid>
      </div>
    )
  }
}

class About extends Component {
  render() {
    return (
      <div>
        <NavBar />
        <Grid>
        <Row>
        <Col xs={12}>
        <PageHeader>About</PageHeader>
        <p>This app has the good stuff</p>
        </Col>
        </Row>
        </Grid>
      </div>
    )
  }
}

class Diary extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: ''
    }
    this.loadData = this.loadData.bind(this);
  }

  loadData(query) {
    const url = 'https://api.nal.usda.gov/ndb/search/?format=json';
    const api_key = '&api_key=730AqE7KBQTgBoA2sh5IB77UuI6smkZdGyOdEDQd&location=Denver+CO'
    axios.get(
      `${url}&q=${query}&sort=n&max=25&offset=0${api_key}`)
      .then(function(res) {
        this.setState({
          data: res.data.list.item
        })
        console.log(data);

      }.bind(this))
      .catch(function(err) {
        console.log(err)
      });
  }

  render() {
    return (
      <div>
        <NavBar />
          <Grid>
            <Row>
              <Col xs={12}>
                <h1>Diaryyy</h1>
                <Search loadData={this.loadData}/>
                <FoodList/>
              </Col>
          </Row>
        </Grid>
      </div>
    )
  }
}

class NavBar extends Component {
  render() {
    return (
      <Navbar collapseOnSelect>
        <Navbar.Header>
          <Navbar.Brand>
            <Link to='/'>Foodiary</Link>
          </Navbar.Brand>
          <Navbar.Toggle />
        </Navbar.Header>
        <Navbar.Collapse>
          <Nav>
            <LinkContainer to='/'>
              <NavItem>Home</NavItem>
            </LinkContainer>
            <LinkContainer to='/about'>
              <NavItem>About</NavItem>
            </LinkContainer>
            <LinkContainer to='/diary'>
              <NavItem>Diary</NavItem>
            </LinkContainer>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    )
  }
}

class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      search: ''
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(e) {
    this.setState({
      search: e.target.value,
      data: ''
    })
  }

  handleSubmit() {
    this.props.loadData(this.state.search)
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        Search:
        <input
          type="text"
          value={this.state.search}
          onChange={this.handleChange}
          />
        <input
          type='submit'
          value='GO'
          />
      </form>
    )
  }
}

class FoodList extends Component {
  constructor(props) {
    super(props);
    this.state = {

    }
  }

  render() {
    return (
      <div>
        <FoodTable/>
      </div>
    )
  }
}

class FoodTable extends Component {
  render() {
    return (
      <table>
        <thead>
          <tr><th>Results</th></tr>
        </thead>
        <tbody>
        </tbody>
      </table>
    )
  }
}

